.loader {
  width: 120px;
  height: 140px;
  z-index: 10;
  perspective: 500px;
  background-image: radial-gradient(30px, #3105f5 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px, #3105f5 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px, #3105f5 100%, rgba(0, 0, 0, 0) 0), linear-gradient(#3105f5 20px, rgba(0, 0, 0, 0) 0);
  background-position: 50% 127px, 94px 102px, 16px 18px, 50% 114px;
  background-repeat: no-repeat;
  background-size: 60px 60px, 10px 10px, 10px 10px, 4px 14px;
  position: relative;
}

.loader:before {
  content: "";
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  border: 3px solid #3105f5;
  border-right-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%)rotate(-45deg);
}

.loader:after {
  content: "";
  height: 80px;
  width: 80px;
  box-sizing: border-box;
  border: 7px solid #47f164;
  border-radius: 50%;
  animation: rotate .5s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%)rotate(-45deg)rotateY(0deg);
}

@keyframes rotate {
  to {
    transform: translate(-50%, -55%)rotate(-45deg)rotateY(360deg);
  }
}

